<template>			
	<el-row type="flex" justify="start">
		<el-collapse v-model="activeNames" @change="handleChange" class="js-editor-head">
			<el-collapse-item title="" name="1">
				 <h3 slot="title"><i class="el-icon-s-help"></i>【 javascprit编辑 】</h3>
				<js-editor></js-editor>
			</el-collapse-item>
		</el-collapse>
		<el-card :body-style="{ padding: '5px' }" class="article">
			<el-col class="article-main">
				<div class="article-title">{{ article.title || ''}}</div>
				<el-button type="primary" icon="el-icon-edit-outline" class="article-edit" v-if="editable" @click.native="edit">编辑</el-button>
				<div class="article-info">
					<span>{{ article.createTime || ''}}</span>
					<span><i class="el-icon-user"></i>{{ article.author.name || ''}}</span>
					<span class="article-count"><i class="el-icon-view"></i>浏览: {{ article.watchCount || 0}}</span>
					<span class="article-good" :class="{'article-liked': articleLiked}" @click.once="likeArticle"><i class="el-icon-thumb"></i>点赞: {{ article.goodCount || 0}}</span>
				</div>
				<div class="dividing_02"></div>
				<div class="article-description">{{ article.description || ''}}</div>
				<div class="markdown-preview" :class="article.theme || 'OneDark'">
					<div v-html="content"></div>
				</div>
				<div class="article-time">
					<span>最后更新: {{ article.updateTime }}</span>
				</div>
			</el-col>
		</el-card>
	</el-row>
</template>

<script>
	import marked from 'marked';
	import hljs from '@/components/markdown/js/hljs';
	import jsEditor from '@/views/user/editor/JsEditor'
	export default {
		name: 'jsContent',
		components:{ jsEditor },
		data(){
			hljs.initHighlightingOnLoad()
			const renderer = new marked.Renderer()
			marked.setOptions({
			  renderer,
			  gfm: true,
			  tables: true,
			  breaks: false,
			  pedantic: false,
			  sanitize: false,
			  smartLists: true,
			  highlight: function (code) {
			    return hljs.highlightAuto(code).value;
			  }
			});
			return {
				article: {author: {
			      id: 0,
			      name: ''
			    }},
				content:'',
				articleLiked: false,
				editable: false,
			}
		},
		methods: {
			likeArticle(){
				(async () => {
				try{
					if(this.articleLiked)
						throw '已点过赞~'
					else if(this.editable)
						throw '不能给自己点赞哦~'
					let data = {
							id: this.article.id
					}
					let res = await this.$api.web.likeArticle(data)
					if(res.status === 2000){
						this.article.goodCount = res.goodCount
						this.articleLiked = true
					}else{
						this.$message.error('服务器繁忙');
					}
					}catch(e){
						if(typeof(e) == 'string')
							this.$message(e)
						else{
							this.$message.error('服务器繁忙');
							console.log(e);
						}

					}
				})()
			},
			edit(){
				this.$router.push('/web' + '/' + this.article.id + '/edit');
			}
		},
		created(){
			(async () => {
				try{
					let res = await this.$api.web.getArticle({id: this.$route.params.id});
					if(res.status === 2000){
						this.article = res.article
						this.editable = res.editable
						this.articleLiked = res.articleLiked
						this.content = marked(res.article.content, {
				          sanitize: false
				        });
					}else{
						this.$message.error('服务器繁忙');
					}
				}catch(e){
					this.$message.error('服务器繁忙');
					console.log(e);
				}
			})()
		}
	}
</script>

<style scoped lang="scss">
	@import '@/assets/public/css/cross-browser.scss';
	@import '@/assets/public/css/box.scss';
	@import '@/assets/public/css/font.scss';
	.el-row{
		flex-flow: column wrap;
	}	
	.js-editor-head{
		padding-left: 10px;
		border: 2px solid rgba(176,224,230,.3);
	}
	.article{
		@include article-main-box;
		margin-bottom: 20px;
		.article-title{
			@include article-title-font;
		}
		.article-info{
			@include info-font;
			text-align: left;
			span{
				margin-right: 20px;
			}
		}
		.article-main{
			text-align: left;
			> * {
				padding: 10px;
				height: 50%;	
			}
		}
		.article-description{
			@include main-text-font;
			padding: 0px 40px 10px 25px;
		}
		.article-count{
		}
		.article-time{
			text-align: right;
			@include info-font;
		}
		.article-good{
			@include goodCount-font;
			margin-left: 10px;
			cursor: pointer;
			&:hover{
				color: #409eff;
			}
		}
		.article-liked{
			color: #409eff;
		}
		.article-edit{
			float: right;
		}
	}
</style>

<style lang="less">
  @import "../css/theme";
  @import "../css/light";
  @import "../css/dark";
  @import "../css/one-dark";
  @import "../css/gitHub";
  @import "../css/index";
</style>


