<template>
	<div class="main">
		<div class="article-code">
			<div class="article-code-header">
				<span><b>JavaScript代码:</b></span>
				<el-button consoleId="asdasd" type="primary" size="small" plain @click.native="runCode(jsConsole[0])"><b class="meduim">执行代码</b></el-button>
				<el-checkbox size="small" v-model="jsConsole[0].noclear" border>结果不清空</el-checkbox>
			</div>
			<div v-highlightjs="jsConsole[0].input" class="article-code-content">
				<el-input
				  type="textarea"
				  placeholder="请输入js代码,使用 console.log() 输出"
				  :autosize="{ minRows: 3}"
				  v-model="jsConsole[0].input"
				  @keydown.native ="textareaTab(jsConsole[0], $event)"
				  style="font-size: 20px;">
				</el-input>	
				<code class="el-textarea__inner javascript highlight-content"></code>
			</div>				
			<div class="article-console">
				<div class="article-console-header">
					<span><b>运行结果:</b></span>
				</div>
				<div style="padding-left: 10px;">
					<span class="red large" v-show="jsConsole[0].error">编译错误！</span>
					<pre style="margin-top:10px" class="large">{{ jsConsole[0].output }}</pre>
				</div>
			</div>	
		</div>
	</div>
</template>

<script>
	export default {
		name: 'WebIndex',
		data(){
			return {
				console1: ['','',false],
				jsConsole : [{
					input: '',
					output: '',
					error: false,
					noclear: false,
					init: false
				}]
			}
		},
		methods: {
			runCode(jsConsole){
				try{
					jsConsole.error = false
					if(!jsConsole.noclear)
						jsConsole.output = ''
					if(!jsConsole.init){
						console.log =  this.$options.methods.withHookBefore(console.log, this.$options.methods.hookConsole, jsConsole)
						jsConsole.init = true
					}
					eval(jsConsole.input)
				}catch(e){
					jsConsole.error = true
					console.warn(e)
				}
			},
			withHookBefore(originalFn, hookFn, jsConsole){
				return function(){
					let _args = [...arguments]
					_args.push(jsConsole)
					hookFn.apply(this, _args)
					return originalFn.apply(this, arguments)
				}
			},
			hookConsole(){
				let pre = arguments[arguments.length-1].output
				let html = ''
				for(let i=0,len=arguments.length-1; i<len; i++)
					html += (arguments[i] + ' ')
				html += '\n'
				arguments[arguments.length-1].output = pre + html
			},
			textareaTab(jsConsole, e){
				let srcElement = e.srcElement
				switch(e.key){
					case 'Tab': {
						insertInputText( srcElement, jsConsole, '\t')
						e.preventDefault()
					}; break;
					case '(': { 
						insertInputText(srcElement, jsConsole, '()', 1)
						e.preventDefault()
					}break;
					case ')': { 
						if(jsConsole.input[srcElement.selectionStart-1]=='('){
							skipInputText(srcElement, jsConsole, ')')
							e.preventDefault()							
						}
					}break;					
					case '{': {
						
						if(jsConsole.input[srcElement.selectionStart] == '\n' || srcElement.selectionStart >= jsConsole.input.length){
							let currentLine = ''
							let startPos = jsConsole.input.slice(0, srcElement.selectionStart).lastIndexOf('\n')		
							if(startPos != -1)
								currentLine = jsConsole.input.slice(startPos, srcElement.selectionStart)
							let tab = '';
							let tabCount = 0;
							for(let i=0, len=currentLine.length; i<len; i++)
								if(currentLine[i] == '\t'){
									tab += '\t'
									tabCount++
								}	
							let insertText = '{\n' + tab +'\t\n' + tab +'}'
							insertInputText(srcElement, jsConsole, insertText, 2+tabCount)
						}
						else{
							insertInputText(srcElement, jsConsole, '{}', 1)
						}

						e.preventDefault()
					}break;					
					case '[': {
						insertInputText(srcElement, jsConsole, '[]', 1)
						e.preventDefault()
					}break;
					case ']': {
						if(jsConsole.input[e.srcElement.selectionStart-1]=='['){
							skipInputText(e.srcElement, jsConsole, ']')
							e.preventDefault()							
						}
					}break;
				}
				function insertInputText(elInput, jsConsole, insertTxt, offset=0) {
					let startPos = elInput.selectionStart;
					let endPos = elInput.selectionEnd;
					if (startPos === undefined || endPos === undefined) return
					let txt = jsConsole.input;
					let result = txt.substring(0, startPos) + insertTxt + txt.substring(endPos)
					jsConsole.input = result;
					elInput.selectionStart = startPos + insertTxt.length;
					elInput.selectionEnd = startPos + insertTxt.length;
					setTimeout(function(){
						elInput.selectionStart = startPos + insertTxt.length - offset;
						elInput.selectionEnd = startPos + insertTxt.length - offset;
					},0)
				}
				function skipInputText(elInput, jsConsole, skipTxt){
					let startPos = elInput.selectionStart;
					let endPos = elInput.selectionEnd;
					let txt = jsConsole.input;
					if (startPos === undefined || endPos === undefined) return
					if(startPos == endPos && txt[startPos] === skipTxt){
						elInput.selectionStart = startPos + 1;
						elInput.selectionEnd = startPos + 1;	
					}			
				}
			}
		},
		created(){

		}
	}	
</script>
<style lang="scss" scoped>
	@import '@/assets/public/css/cross-browser.scss';
	.article-code .el-textarea .el-textarea__inner{
		color: #606266; /* 光标的颜色*/
		text-shadow: 0px 0px 0px rgba(0, 0, 0, 0); 
		-webkit-text-fill-color: transparent;
		&::-webkit-input-placeholder, &::-moz-placeholder{
				color: #000; /* 改变placeholder文本颜色 */
				text-shadow: none;
				-webkit-text-fill-color: initial;
		}
	}
	@include selection-all(rgba(40,206,113,.5));
</style>
<style lang="scss" scoped>
	/*@import url('https://cdn.bootcss.com/highlight.js/9.15.6/styles/atelier-forest-light.min.css');*/
	.main{
		border: 1px solid #efefef;
		padding: 5px 15px 5px 5px;
	}
	.article-head{
		background: #fbfbfb;
		line-height: 24px;
		padding: 10px 30px 10px 10px;
		border-top: 1px solid #efefef;
		border-bottom: 1px solid #efefef;
		color: #8590a6;
		font-size: 25px;
		box-shadow: 0 1px 3px rgba(0,37,55,.05);
		box-sizing: border-box;
	}
	.article-code{
		border: 1px solid #ddd;
		margin: 10px 30px 10px 10px;
		font-family: Menlo,Monaco,Consolas,"Andale Mono","lucida console","Courier New",monospace;
		.article-code-header,.article-console-header{
			background: #f3f6fb;
			padding: 10px
		}
	}
	.article-code-content{
		font-size: 20px;
		position: relative;
		margin: 0px;
		padding: 0px;
		border: none;
		background-color: rgba(0, 0, 0, 0);
	}
	.highlight-content{
		pointer-events: none;
		position: absolute;
		left: 0px;
		top: 0px;
		min-height: calc(6em - 18px);
		background-color: rgba(0, 0, 0, 0);
		border: 1px solid rgba(0,0,0,0);
		margin: 1px 0px;
		padding: 5px 15px;
		
		-webkit-appearance: textarea;
		-webkit-rtl-ordering: logical;
		-webkit-writing-mode: horizontal-tb !important;
		flex-direction: column;
		white-space: pre-wrap;
		word-wrap: break-word;
		text-rendering: auto;
		letter-spacing: normal;
		word-spacing: normal;
		text-transform: none;
		text-indent: 0px;
		text-shadow: none;
		text-align: start;
	}
</style>


