<template>
	<el-container class="wrap">	
		<el-header style="height: auto"><app-header></app-header></el-header>
		<el-container class="content">
			<el-aside>
				Aside 
	 			<br/>先占个广告位吧
	 		</el-aside>
		 	<el-main class="content">
			    <app-article></app-article>			   	
		 	</el-main>
		</el-container>
		<el-footer style="height: auto"><app-footer></app-footer></el-footer>
  </el-container>
</template>

<script>
import appHeader from '@/views/public/Header';
import appFooter from '@/views/public/Footer';
import appArticle from '@/views/web/article/Article'
export default {
  name: "App",
  components: {appHeader, appFooter, appArticle},
};
</script>

<style lang="scss" scoped>
	@import '@/assets/public/css/box.scss';
	.el-header{
		@include main-header;
	}
	.el-main {
		@include main-body;
	}
	.el-aside{
		@include main-aside;
	}
	.el-footer{
		@include main-footer;
	}
</style>



